interface IAudioData {
    size: number;
    buffer: any;
    inputSampleRate: number;
    inputSampleBits: number;
    outputSampleRate: number;
    oututSampleBits: number;
    input: (data: any) => void;
    encodeWAV: () => Blob;
    compress: () => Float32Array;
    reset: () => void;
}

export interface AudioBlob {
    duration: number;
    blob: Blob
}

class MediaRecorder {
    audioInput: MediaStreamAudioSourceNode;
    recorder: ScriptProcessorNode;
    context: AudioContext;
    audioData: IAudioData;

    canRecording = (navigator.getUserMedia != null);

    constructor(stream: MediaStream, config?: any) {
        const $this = this;

        config = config || {};
        config.sampleBits = config.sampleBits || 16;   // 8, 16
        config.sampleRate = config.sampleRate || (8000);  //(1/6 44100) 

        this.context = new AudioContext();
        this.audioInput = this.context.createMediaStreamSource(stream);
        this.recorder = this.context.createScriptProcessor(4096, 1, 1);

        const inputSampleRate = this.context.sampleRate;
        const outputSampleRate = config.sampleRate;

        this.audioData = {
            size: 0     //
            , buffer: []   //
            , inputSampleRate: inputSampleRate  //
            , inputSampleBits: 16    // 8, 16
            , outputSampleRate: outputSampleRate  //
            , oututSampleBits: config.sampleBits    // 8, 16
            , input: function (data: any) {
                this.buffer.push(new Float32Array(data));
                this.size += data.length;
            }
            , compress: function () { //
                //
                var data = new Float32Array(this.size);
                var offset = 0;
                for (var i = 0; i < this.buffer.length; i++) {
                    data.set(this.buffer[i], offset);
                    offset += this.buffer[i].length;
                }
                //
                var compression = parseInt(inputSampleRate / outputSampleRate + '');
                var length = data.length / compression;
                var result = new Float32Array(length);
                var index = 0, j = 0;
                while (index < length) {
                    result[index] = data[j];
                    j += compression;
                    index++;
                }
                return result;
            }
            , encodeWAV: function () {
                var sampleRate = Math.min(this.inputSampleRate, this.outputSampleRate);
                var sampleBits = Math.min(this.inputSampleBits, this.oututSampleBits);
                var bytes = this.compress();
                var dataLength = bytes.length * (sampleBits / 8);
                var buffer = new ArrayBuffer(44 + dataLength);
                var data = new DataView(buffer);


                var channelCount = 1;//
                var offset = 0;


                var writeString = function (str: string) {
                    for (var i = 0; i < str.length; i++) {
                        data.setUint8(offset + i, str.charCodeAt(i));
                    }
                }

                // 
                writeString('RIFF'); offset += 4;
                // ,-8
                data.setUint32(offset, 36 + dataLength, true); offset += 4;
                // WAV
                writeString('WAVE'); offset += 4;
                // 
                writeString('fmt '); offset += 4;
                // , 0x10 = 16
                data.setUint32(offset, 16, true); offset += 4;
                //  (PCM)
                data.setUint16(offset, 1, true); offset += 2;
                // 
                data.setUint16(offset, channelCount, true); offset += 2;
                // ,,
                data.setUint32(offset, sampleRate, true); offset += 4;
                //  () /8
                data.setUint32(offset, channelCount * sampleRate * (sampleBits / 8), true); offset += 4;
                //   /8
                data.setUint16(offset, channelCount * (sampleBits / 8), true); offset += 2;
                // 
                data.setUint16(offset, sampleBits, true); offset += 2;
                // 
                writeString('data'); offset += 4;
                // ,-44
                data.setUint32(offset, dataLength, true); offset += 4;
                // 
                if (sampleBits === 8) {
                    for (var i = 0; i < bytes.length; i++ , offset++) {
                        var s = Math.max(-1, Math.min(1, bytes[i]));
                        var val = s < 0 ? s * 0x8000 : s * 0x7FFF;
                        val = parseInt(255 / (65535 / (val + 32768)) + '');
                        data.setInt8(offset, val);
                    }
                } else {
                    for (var i = 0; i < bytes.length; i++ , offset += 2) {
                        var s = Math.max(-1, Math.min(1, bytes[i]));
                        data.setInt16(offset, s < 0 ? s * 0x8000 : s * 0x7FFF, true);
                    }
                }


                return new Blob([data], { type: 'audio/wav' });
            },
            reset: function () {
                this.size = 0;
                this.buffer = [];
            }
        };

        //
        this.recorder.onaudioprocess = function (e) {
            $this.audioData.input(e.inputBuffer.getChannelData(0));
            //record(e.inputBuffer.getChannelData(0));
        }

    }

    //
    start() {
        this.audioInput.connect(this.recorder);
        this.recorder.connect(this.context.destination);
    }


    //
    stop() {
        this.audioInput.disconnect();
        this.recorder.disconnect();
    }


    //
    getBlobAsync() {
        this.stop();
        const blob = this.audioData.encodeWAV();
        const tempAudio = document.createElement('audio');
        const durationP = new Promise<AudioBlob>(resolve => {
            tempAudio.addEventListener('loadedmetadata', () => {
                // Chrome bug: https://bugs.chromium.org/p/chromium/issues/detail?id=642012
                if (tempAudio.duration === Infinity) {
                    tempAudio.currentTime = Number.MAX_SAFE_INTEGER
                    tempAudio.ontimeupdate = () => {
                        tempAudio.ontimeupdate = null
                        resolve({ blob, duration: tempAudio.duration })
                        tempAudio.currentTime = 0
                    }
                }
                // Normal behavior
                else
                    resolve({ blob, duration: tempAudio.duration })
            })
        });

        tempAudio.src = window.URL.createObjectURL(blob);

        return durationP;
    }

    reset() {
        this.audioData.size = 0;
        this.audioData.buffer = [];
    }

    //
    play(audio: HTMLAudioElement) {
        this.getBlobAsync().then(({ blob }) => audio.src = window.URL.createObjectURL(blob))
    }


    //
    upload(url: string, callback: (parame: any, e: any) => void) {
        var fd = new FormData();
        this.getBlobAsync()
            .then(data => {
                const { blob } = data;
                fd.append("audioData", blob);
                var xhr = new XMLHttpRequest();
                if (callback) {
                    xhr.upload.addEventListener("progress", function (e) {
                        callback('uploading', e);
                    }, false);
                    xhr.addEventListener("load", function (e) {
                        callback('ok', e);
                    }, false);
                    xhr.addEventListener("error", function (e) {
                        callback('error', e);
                    }, false);
                    xhr.addEventListener("abort", function (e) {
                        callback('cancel', e);
                    }, false);
                }
                xhr.open("POST", url);
                xhr.send(fd);
            })
            .catch(reason => {
                throw reason;
            });

    }
    // 
    static get(callback: (rec: MediaRecorder) => void, error?: (errMessage: string) => void, config?: any) {
        const throwError = (message: string) => {
            error && error(message);
        }

        if (callback) {
            if (navigator.getUserMedia) {
                navigator.getUserMedia(
                    { audio: true } //
                    , function (stream) {
                        var rec = new MediaRecorder(stream, config);
                        callback(rec);
                    }
                    , function (error: any) {
                        console.error(error);
                        switch (error.code || error.name) {
                            case 'PERMISSION_DENIED':
                            case 'PermissionDeniedError':
                                throwError('');
                                break;
                            case 'NOT_SUPPORTED_ERROR':
                            case 'NotSupportedError':
                                throwError('');
                                break;
                            case 'MANDATORY_UNSATISFIED_ERROR':
                            case 'MandatoryUnsatisfiedError':
                                throwError('');
                                break;
                            default:
                                throwError(':' + (error.code || error.name));
                                break;
                        }
                    });
            } else {
                throwError(''); return;
            }
        }
    }


}

export default MediaRecorder;